import RepairFn from '../../api/repair/index.js';
import tool from '../../utils/index.js'
export default {
	data() {
		return {
			userinfo:{},
			form: {
				repairid:0,
				dealcontent:'',
				dealuser:'',
				dealusertel:'',
				note:'',
				images:[]
			},
			rules:{
				dealcontent:[{
					required: true,
					message: '请填写维修情况'
				}],
				dealuser:[{required: true,message: '请填写维修人'}],
				dealusertel:[{required: true,message: '请填写联系方式'}]
			}
		}
	},
	mounted() {
		this.$refs.form.setRules(this.rules);
		this.inituserinfo();
	},
	onLoad(e) {
		this.form.repairid = e.id;
	},
	methods: {
		inituserinfo(){
			const userinfo = uni.getStorageSync('userinfo');
			this.userinfo = userinfo;
			this.form.dealuser = userinfo.name;
			this.form.dealusertel = userinfo.tel;
		},
		opensheet() {
			const that = this;
			uni.showActionSheet({
			    itemList: ['相册'],
			    success: function (res) {
			        let index = res.tapIndex;
					switch (index){
						case 0:
							that.chooseimg();
							break;
						default:
							break;
					}
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		},
		chooseimg(){
			const that = this;
			uni.chooseImage({
			    count: 9, //默认9
			    sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
					const tempFilePaths = res.tempFilePaths;
					//删除文件
					RepairFn.removedetailimgs({
						"images":that.form.images
					}).then(res=>{
						that.form.images=[];
						//上传文件
						tempFilePaths.forEach(function(item){
							uni.showLoading({
								title:"文件上传中……"
							});
							uni.uploadFile({
							    url: RepairFn.repairdetail_uploadimgurl,
								header:tool.getheader(),
							    filePath: item,
							    name: 'file',
							    success: (uploadFileRes) => {
									let resdata = JSON.parse(uploadFileRes.data);
									uni.showToast({
										title:resdata.msg,
										duration:2000
									});
									uni.hideLoading();
									that.form.images.push(resdata.result);
							    }
							});
						});
					});   
			    }
			});
		},
		submit_form(){
			this.$refs.form.validate((v) => {
				if (v) {
					RepairFn.addrepair_detail(this.form).then(res=>{
						let result = res;
						uni.showToast({
							title:result.data.msg,
							duration:3000,
						});
						if(result.data.code === 1){
							uni.navigateBack();
						}
					});
				}
			});
		}
	},
}
